"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import type { Assignment } from "@/lib/types";
import { Plus, Trash2 } from "lucide-react";
import { cn } from "@/lib/utils";

const priorityLabel = { low: "低", medium: "中", high: "高" };
const priorityColor = { low: "bg-muted text-muted-foreground", medium: "bg-blue-500/15 text-blue-700 dark:text-blue-300", high: "bg-red-500/15 text-red-700 dark:text-red-300" };

function formatDate(s: string) {
  return new Date(s).toLocaleDateString("ja-JP", { month: "short", day: "numeric", weekday: "short" });
}

function daysUntil(date: string) {
  const today = new Date();
  today.setHours(0, 0, 0, 0);
  const d = new Date(date + "T00:00:00");
  const diff = d.getTime() - today.getTime();
  return Math.floor(diff / (1000 * 60 * 60 * 24));
}

function countdownLabel(due: string, completed: boolean) {
  if (completed) return "完了済み";
  const d = daysUntil(due);
  if (d < 0) return "期限切れ";
  if (d === 0) return "今日が期限";
  if (d === 1) return "あと1日";
  return `あと${d}日`;
}

export function AssignmentsSection({ assignments, onUpdate }: { assignments: Assignment[]; onUpdate: (a: Assignment[]) => void }) {
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState("");
  const [subject, setSubject] = useState("");
  const [dueDate, setDueDate] = useState("");
  const [priority, setPriority] = useState<Assignment["priority"]>("medium");

  const add = () => {
    if (!title.trim() || !dueDate) return;
    onUpdate([...assignments, { id: crypto.randomUUID(), title: title.trim(), subject: subject.trim() || undefined, dueDate, priority, completed: false }]);
    setTitle("");
    setSubject("");
    setDueDate("");
    setPriority("medium");
    setOpen(false);
  };

  const toggle = (id: string) => {
    onUpdate(assignments.map((a) => (a.id === id ? { ...a, completed: !a.completed } : a)));
  };

  const remove = (id: string) => onUpdate(assignments.filter((a) => a.id !== id));

  const sorted = [...assignments].sort((a, b) => new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime());
  const pending = sorted.filter((a) => !a.completed);
  const done = sorted.filter((a) => a.completed);
  const isOverdue = (due: string, completed: boolean) => !completed && new Date(due) < new Date();

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between space-y-0">
        <div>
          <CardTitle>大学課題</CardTitle>
          <CardDescription>レポート・提出物の締切を管理</CardDescription>
        </div>
        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger asChild><Button size="sm" className="gap-1.5"><Plus className="size-4" /> 追加</Button></DialogTrigger>
          <DialogContent>
            <DialogHeader><DialogTitle>課題を追加</DialogTitle></DialogHeader>
            <div className="grid gap-4 py-2">
              <div className="grid gap-2"><label className="text-sm font-medium">課題名</label><Input placeholder="例: レポート第1章" value={title} onChange={(e) => setTitle(e.target.value)} /></div>
              <div className="grid gap-2"><label className="text-sm font-medium">科目（任意）</label><Input placeholder="例: 経済学入門" value={subject} onChange={(e) => setSubject(e.target.value)} /></div>
              <div className="grid grid-cols-2 gap-4">
                <div className="grid gap-2"><label className="text-sm font-medium">締切</label><Input type="date" value={dueDate} onChange={(e) => setDueDate(e.target.value)} /></div>
                <div className="grid gap-2"><label className="text-sm font-medium">優先度</label><Select value={priority} onValueChange={(v) => setPriority(v as Assignment["priority"])}><SelectTrigger><SelectValue /></SelectTrigger><SelectContent><SelectItem value="low">低</SelectItem><SelectItem value="medium">中</SelectItem><SelectItem value="high">高</SelectItem></SelectContent></Select></div>
              </div>
            </div>
            <DialogFooter><Button variant="outline" onClick={() => setOpen(false)}>キャンセル</Button><Button onClick={add} disabled={!title.trim() || !dueDate}>追加</Button></DialogFooter>
          </DialogContent>
        </Dialog>
      </CardHeader>
      <CardContent className="space-y-4">
        {assignments.length === 0 ? <p className="text-muted-foreground text-center text-sm py-8">課題を追加するとここに表示されます</p> : (
          <>
            {pending.length > 0 && (
              <ul className="space-y-2">
                {pending.map((a) => (
                  <li key={a.id} className={cn("flex items-center gap-3 rounded-lg border p-3", isOverdue(a.dueDate, a.completed) && "border-destructive/50 bg-destructive/5")}>
                    <Checkbox checked={a.completed} onCheckedChange={() => toggle(a.id)} />
                    <div className="min-w-0 flex-1">
                      <p className="font-medium truncate">{a.title}</p>
                      <p className="text-muted-foreground text-xs">
                        {a.subject && `${a.subject} · `}
                        {formatDate(a.dueDate)} · {countdownLabel(a.dueDate, a.completed)}
                      </p>
                    </div>
                    <Badge variant="secondary" className={priorityColor[a.priority]}>{priorityLabel[a.priority]}</Badge>
                    <Button variant="ghost" size="icon" className="shrink-0 text-muted-foreground hover:text-destructive" onClick={() => remove(a.id)}><Trash2 className="size-4" /></Button>
                  </li>
                ))}
              </ul>
            )}
            {done.length > 0 && (
              <div><p className="text-muted-foreground mb-2 text-xs font-medium">完了 ({done.length})</p>
                <ul className="space-y-2">
                  {done.map((a) => (
                    <li key={a.id} className="flex items-center gap-3 rounded-lg border border-dashed p-3 opacity-80">
                      <Checkbox checked onCheckedChange={() => toggle(a.id)} />
                      <div className="min-w-0 flex-1"><p className="truncate text-sm line-through text-muted-foreground">{a.title}</p><p className="text-muted-foreground text-xs">{formatDate(a.dueDate)}</p></div>
                      <Button variant="ghost" size="icon" className="shrink-0" onClick={() => remove(a.id)}><Trash2 className="size-4" /></Button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </>
        )}
      </CardContent>
    </Card>
  );
}
