"use client";

import { useState, useEffect, useCallback } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog";
import { Calendar } from "@/components/ui/calendar";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import type { StudyLog } from "@/lib/types";
import { Plus, Play, Pause, Square } from "lucide-react";
import { PomodoroTimer } from "./PomodoroTimer";
import { StudyTimeChart } from "./StudyTimeChart";

function formatDuration(minutes: number) {
  const h = Math.floor(minutes / 60);
  const m = minutes % 60;
  return h === 0 ? `${m}分` : `${h}時間${m}分`;
}

function formatDate(s: string) {
  return new Date(s).toLocaleDateString("ja-JP", { month: "short", day: "numeric", weekday: "short" });
}

export function StudyTimeSection({ logs, onUpdate }: { logs: StudyLog[]; onUpdate: (logs: StudyLog[]) => void }) {
  const [seconds, setSeconds] = useState(0);
  const [running, setRunning] = useState(false);
  const [subject, setSubject] = useState("");
  const [dialogOpen, setDialogOpen] = useState(false);
  const [logMinutes, setLogMinutes] = useState("");
  const [logDate, setLogDate] = useState("");
  const [logSubject, setLogSubject] = useState("");
  const [logMemo, setLogMemo] = useState("");
  const [chartDays, setChartDays] = useState(14);

  // これまでの記録から「作業項目」の候補を抽出
  const workItems = Array.from(
    new Set(
      logs
        .map((l) => l.subject?.trim())
        .filter((s): s is string => !!s && s.length > 0 && s !== "ポモドーロ")
    )
  );

  const getTodayYmd = () => new Date().toISOString().slice(0, 10);

  useEffect(() => {
    if (!running) return;
    const id = setInterval(() => setSeconds((s) => s + 1), 1000);
    return () => clearInterval(id);
  }, [running]);

  const stop = useCallback(() => {
    setRunning(false);
    const totalMinutes = Math.floor(seconds / 60);
    if (totalMinutes > 0) {
      const today = getTodayYmd();
      onUpdate([...logs, { id: crypto.randomUUID(), date: today, minutes: totalMinutes, subject: subject.trim() || undefined }]);
    }
    setSeconds(0);
    setSubject("");
  }, [seconds, subject, logs, onUpdate]);

  const addManual = () => {
    const min = parseInt(logMinutes, 10);
    if (Number.isNaN(min) || min < 1) return;
    const dateToUse = logDate || getTodayYmd();
    onUpdate([...logs, { id: crypto.randomUUID(), date: dateToUse, minutes: min, subject: logSubject.trim() || undefined, memo: logMemo.trim() || undefined }]);
    setLogMinutes("");
    setLogDate("");
    setLogSubject("");
    setLogMemo("");
    setDialogOpen(false);
  };

  const handlePomodoroComplete = useCallback((minutes: number) => {
    const today = getTodayYmd();
    onUpdate([...logs, { id: crypto.randomUUID(), date: today, minutes, subject: "ポモドーロ" }]);
  }, [logs, onUpdate]);

  const totalToday = logs.filter((l) => l.date === getTodayYmd()).reduce((acc, l) => acc + l.minutes, 0);
  const totalThisWeek = logs.reduce((acc, l) => {
    const d = new Date(l.date);
    const now = new Date();
    const startOfWeek = new Date(now);
    startOfWeek.setDate(now.getDate() - now.getDay());
    startOfWeek.setHours(0, 0, 0, 0);
    return d >= startOfWeek ? acc + l.minutes : acc;
  }, 0);

  const displayMin = Math.floor(seconds / 60);
  const displaySec = seconds % 60;

  // 作業項目ごとのサマリ（今日・今週・合計）
  const subjectSummary = (() => {
    const map = new Map<
      string,
      { name: string; today: number; week: number; total: number }
    >();
    const today = getTodayYmd();
    const now = new Date();
    const startOfWeek = new Date(now);
    startOfWeek.setDate(now.getDate() - now.getDay());
    startOfWeek.setHours(0, 0, 0, 0);

    for (const log of logs) {
      const name = log.subject?.trim() || "未分類";
      const entry =
        map.get(name) ?? { name, today: 0, week: 0, total: 0 };
      entry.total += log.minutes;
      if (log.date === today) entry.today += log.minutes;
      const d = new Date(log.date);
      if (d >= startOfWeek) entry.week += log.minutes;
      map.set(name, entry);
    }

    return Array.from(map.values()).sort(
      (a, b) => b.total - a.total
    );
  })();

  return (
    <div className="space-y-6">
      <div>
        <p className="text-muted-foreground mb-2 text-center text-sm">25分集中 → 5分休憩を繰り返し、4セット後に15分の長休憩</p>
        <PomodoroTimer onPomodoroComplete={handlePomodoroComplete} />
      </div>
      <Card>
        <CardHeader>
          <CardTitle>勉強タイマー</CardTitle>
          <CardDescription>作業項目ごとに時間を計測して記録に追加</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="rounded-xl border-2 border-muted bg-muted/30 p-6 text-center">
            <p className="font-mono text-4xl font-bold tabular-nums sm:text-5xl">{String(displayMin).padStart(2, "0")}:{String(displaySec).padStart(2, "0")}</p>
            <p className="text-muted-foreground mt-1 text-sm">{running ? "計測中" : "停止中"}</p>
            {/* 作業項目の選択／入力 */}
            <div className="mt-3 space-y-2">
              {workItems.length > 0 && (
                <div className="flex justify-center">
                  <Select value={subject} onValueChange={setSubject}>
                    <SelectTrigger className="w-60 justify-between text-left text-xs sm:text-sm" size="sm">
                      <SelectValue placeholder="作業項目を選択" />
                    </SelectTrigger>
                    <SelectContent>
                      {workItems.map((item) => (
                        <SelectItem key={item} value={item}>
                          {item}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              )}
              <div className="max-w-xs mx-auto">
                <Input
                  placeholder="作業項目を入力（例: TOEIC Part3, レポートA）"
                  value={subject}
                  onChange={(e) => setSubject(e.target.value)}
                />
              </div>
            </div>
            <div className="mt-4 flex justify-center gap-2">
              {!running ? <Button onClick={() => setRunning(true)} className="gap-2"><Play className="size-4" /> 開始</Button> : (
                <><Button variant="outline" onClick={() => setRunning(false)} className="gap-2"><Pause className="size-4" /> 一時停止</Button><Button onClick={stop} className="gap-2"><Square className="size-4" /> 終了して記録</Button></>
              )}
            </div>
          </div>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0">
          <div><CardTitle>勉強時間の記録</CardTitle><CardDescription>今日・今週の合計と履歴</CardDescription></div>
          <Dialog open={dialogOpen} onOpenChange={(open) => { setDialogOpen(open); if (open && !logDate) setLogDate(getTodayYmd()); }}>
            <DialogTrigger asChild><Button size="sm" className="gap-1.5"><Plus className="size-4" /> 手動追加</Button></DialogTrigger>
            <DialogContent>
              <DialogHeader><DialogTitle>勉強時間を手動で追加</DialogTitle></DialogHeader>
              <div className="grid gap-4 py-2">
                <div className="grid gap-2">
                  <label className="text-sm font-medium">勉強時間（分）</label>
                  <Input type="number" min={1} placeholder="例: 45" value={logMinutes} onChange={(e) => setLogMinutes(e.target.value)} />
                </div>
                <div className="grid gap-2">
                  <label className="text-sm font-medium">日付</label>
                  <p className="text-muted-foreground text-xs">
                    {logDate ? new Date(logDate + "T12:00:00").toLocaleDateString("ja-JP", { year: "numeric", month: "long", day: "numeric", weekday: "short" }) : "今日の日付で追加されます"}
                  </p>
                  <div className="flex justify-center rounded-md border bg-muted/30 p-2">
                    <Calendar
                      mode="single"
                      selected={logDate ? new Date(logDate + "T12:00:00") : undefined}
                      onSelect={(d) => d && setLogDate(d.toISOString().slice(0, 10))}
                      defaultMonth={logDate ? new Date(logDate + "T12:00:00") : new Date()}
                    />
                  </div>
                </div>
                <div className="grid gap-2"><label className="text-sm font-medium">作業項目（任意）</label><Input placeholder="例: 経済学, レポートA" value={logSubject} onChange={(e) => setLogSubject(e.target.value)} /></div>
                <div className="grid gap-2"><label className="text-sm font-medium">メモ（任意）</label><Input placeholder="例: 第3章復習" value={logMemo} onChange={(e) => setLogMemo(e.target.value)} /></div>
              </div>
              <DialogFooter>
                <Button variant="outline" onClick={() => setDialogOpen(false)}>キャンセル</Button>
                <Button onClick={addManual} disabled={!logMinutes || Number.isNaN(parseInt(logMinutes, 10)) || parseInt(logMinutes, 10) < 1}>追加</Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="rounded-lg border bg-muted/50 p-4"><p className="text-muted-foreground text-xs">今日</p><p className="text-xl font-bold">{formatDuration(totalToday)}</p></div>
            <div className="rounded-lg border bg-muted/50 p-4"><p className="text-muted-foreground text-xs">今週</p><p className="text-xl font-bold">{formatDuration(totalThisWeek)}</p></div>
          </div>
          <div>
            <div className="mb-2 flex items-center justify-between"><p className="text-muted-foreground text-sm font-medium">作業時間のグラフ</p><select className="rounded-md border border-input bg-background px-2 py-1 text-xs text-foreground" value={chartDays} onChange={(e) => setChartDays(Number(e.target.value))}><option value={7}>直近7日</option><option value={14}>直近14日</option><option value={30}>直近30日</option></select></div>
            <StudyTimeChart logs={logs} days={chartDays} />
          </div>
          {logs.length === 0 ? <p className="text-muted-foreground text-center text-sm py-4">記録がありません。タイマーで計測するか手動で追加してください。</p> : (
            <ul className="space-y-2">{[...logs].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime()).slice(0, 10).map((log) => (<li key={log.id} className="flex justify-between rounded-lg border px-3 py-2 text-sm"><span>{formatDate(log.date)}{log.subject && ` · ${log.subject}`}</span><span className="font-medium">{formatDuration(log.minutes)}</span></li>))}</ul>
          )}
        </CardContent>
      </Card>
      {subjectSummary.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>作業項目別サマリ</CardTitle>
            <CardDescription>今日・今週・累計の勉強時間を作業別に集計</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="overflow-x-auto">
              <table className="w-full text-left text-sm">
                <thead className="border-b text-xs text-muted-foreground">
                  <tr>
                    <th className="py-1.5 pr-4">作業項目</th>
                    <th className="py-1.5 pr-4 text-right">今日</th>
                    <th className="py-1.5 pr-4 text-right">今週</th>
                    <th className="py-1.5 pr-2 text-right">合計</th>
                  </tr>
                </thead>
                <tbody>
                  {subjectSummary.map((row) => (
                    <tr key={row.name} className="border-b last:border-0">
                      <td className="py-1.5 pr-4">{row.name}</td>
                      <td className="py-1.5 pr-4 text-right">
                        {row.today > 0 ? formatDuration(row.today) : "-"}
                      </td>
                      <td className="py-1.5 pr-4 text-right">
                        {row.week > 0 ? formatDuration(row.week) : "-"}
                      </td>
                      <td className="py-1.5 pr-2 text-right">
                        {formatDuration(row.total)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
