"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Progress } from "@/components/ui/progress";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import type { QualificationRecord, QualificationGoal, QualificationPractice } from "@/lib/types";
import { QUALIFICATION_PRESETS } from "@/lib/types";
import { Plus, Target, BookOpen, Trash2 } from "lucide-react";

function formatDate(s: string) {
  return new Date(s).toLocaleDateString("ja-JP", { year: "numeric", month: "short", day: "numeric" });
}

function isToeic(name: string) {
  return name === "TOEIC";
}

function recordDisplay(r: QualificationRecord) {
  if (r.toeicListening != null && r.toeicReading != null) return `${r.toeicListening + r.toeicReading}点 (L:${r.toeicListening} R:${r.toeicReading})`;
  return r.result ?? "—";
}

type Props = {
  records: QualificationRecord[];
  goals: QualificationGoal[];
  practices: QualificationPractice[];
  onUpdate: (payload: { qualificationRecords?: QualificationRecord[]; qualificationGoals?: QualificationGoal[]; qualificationPractices?: QualificationPractice[] }) => void;
};

export function QualificationSection({ records, goals, practices, onUpdate }: Props) {
  const [scoreOpen, setScoreOpen] = useState(false);
  const [goalOpen, setGoalOpen] = useState(false);
  const [practiceOpen, setPracticeOpen] = useState(false);
  const [qualName, setQualName] = useState("");
  const [customName, setCustomName] = useState("");
  const [listening, setListening] = useState("");
  const [reading, setReading] = useState("");
  const [resultText, setResultText] = useState("");
  const [scoreDate, setScoreDate] = useState("");
  const [goalQualName, setGoalQualName] = useState("");
  const [customGoalName, setCustomGoalName] = useState("");
  const [goalTarget, setGoalTarget] = useState("");
  const [goalTargetDate, setGoalTargetDate] = useState("");
  const [practiceQualName, setPracticeQualName] = useState("");
  const [practiceCustomName, setPracticeCustomName] = useState("");
  const [practiceMinutes, setPracticeMinutes] = useState("");
  const [practiceType, setPracticeType] = useState<"listening" | "reading" | "both">("both");
  const [practiceDate, setPracticeDate] = useState("");

  const resolvedQualName = qualName === "その他" ? customName.trim() : qualName;

  const addRecord = () => {
    if (!resolvedQualName || !scoreDate) return;
    if (isToeic(resolvedQualName)) {
      const l = parseInt(listening, 10);
      const r = parseInt(reading, 10);
      if (Number.isNaN(l) || Number.isNaN(r)) return;
      onUpdate({ qualificationRecords: [...records, { id: crypto.randomUUID(), name: resolvedQualName, date: scoreDate, toeicListening: l, toeicReading: r }] });
      setListening("");
      setReading("");
    } else {
      if (!resultText.trim()) return;
      onUpdate({ qualificationRecords: [...records, { id: crypto.randomUUID(), name: resolvedQualName, date: scoreDate, result: resultText.trim() }] });
      setResultText("");
    }
    setScoreDate("");
    setQualName("");
    setCustomName("");
    setScoreOpen(false);
  };

  const addGoal = () => {
    const name = goalQualName === "その他" ? customGoalName.trim() : goalQualName;
    if (!name || !goalTarget.trim() || !goalTargetDate) return;
    onUpdate({ qualificationGoals: [...goals, { id: crypto.randomUUID(), qualificationName: name, target: goalTarget.trim(), targetDate: goalTargetDate }] });
    setGoalQualName("");
    setCustomGoalName("");
    setGoalTarget("");
    setGoalTargetDate("");
    setGoalOpen(false);
  };

  const addPractice = () => {
    const name = practiceQualName === "その他" ? practiceCustomName.trim() || "その他" : practiceQualName;
    if (!name || !practiceDate) return;
    const min = parseInt(practiceMinutes, 10);
    if (Number.isNaN(min) || min < 1) return;
    onUpdate({ qualificationPractices: [...practices, { id: crypto.randomUUID(), qualificationName: name, date: practiceDate, minutes: min, ...(isToeic(name) ? { type: practiceType } : {}) }] });
    setPracticeQualName("");
    setPracticeCustomName("");
    setPracticeMinutes("");
    setPracticeDate("");
    setPracticeOpen(false);
  };

  const latestByQual = new Map<string, QualificationRecord>();
  for (const r of records) {
    const existing = latestByQual.get(r.name);
    if (!existing || new Date(r.date) > new Date(existing.date)) latestByQual.set(r.name, r);
  }

  const goalProgress = (goal: QualificationGoal) => {
    const latest = latestByQual.get(goal.qualificationName);
    if (!latest || !isToeic(goal.qualificationName)) return null;
    const total = (latest.toeicListening ?? 0) + (latest.toeicReading ?? 0);
    const match = goal.target.match(/\d+/);
    const targetNum = match ? parseInt(match[0], 10) : 0;
    return targetNum > 0 ? Math.min(100, Math.round((total / targetNum) * 100)) : null;
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0">
          <div>
            <CardTitle className="flex items-center gap-2"><BookOpen className="size-5" /> 資格の記録</CardTitle>
            <CardDescription>スコア・合格結果を記録（TOEIC / 英検 / 簿記など）</CardDescription>
          </div>
          <div className="flex gap-2">
            <Dialog open={goalOpen} onOpenChange={setGoalOpen}>
              <DialogTrigger asChild><Button variant="outline" size="sm" className="gap-1.5"><Target className="size-4" /> 目標</Button></DialogTrigger>
              <DialogContent>
                <DialogHeader><DialogTitle>目標を設定</DialogTitle></DialogHeader>
                <div className="grid gap-4 py-2">
                  <div className="grid gap-2"><label className="text-sm font-medium">資格</label><Select value={goalQualName} onValueChange={setGoalQualName}><SelectTrigger><SelectValue placeholder="選択" /></SelectTrigger><SelectContent>{QUALIFICATION_PRESETS.map((q) => <SelectItem key={q} value={q}>{q}</SelectItem>)}</SelectContent></Select>{goalQualName === "その他" && <Input placeholder="資格名を入力" value={customGoalName} onChange={(e) => setCustomGoalName(e.target.value)} />}</div>
                  <div className="grid gap-2"><label className="text-sm font-medium">目標（例: 800点, 2級）</label><Input placeholder="例: 800点" value={goalTarget} onChange={(e) => setGoalTarget(e.target.value)} /></div>
                  <div className="grid gap-2"><label className="text-sm font-medium">目標日</label><Input type="date" value={goalTargetDate} onChange={(e) => setGoalTargetDate(e.target.value)} /></div>
                </div>
                <DialogFooter><Button variant="outline" onClick={() => setGoalOpen(false)}>キャンセル</Button><Button onClick={addGoal} disabled={!goalQualName || !goalTarget.trim() || !goalTargetDate || (goalQualName === "その他" && !customGoalName.trim())}>追加</Button></DialogFooter>
              </DialogContent>
            </Dialog>
            <Dialog open={scoreOpen} onOpenChange={setScoreOpen}>
              <DialogTrigger asChild><Button size="sm" className="gap-1.5"><Plus className="size-4" /> 結果を追加</Button></DialogTrigger>
              <DialogContent>
                <DialogHeader><DialogTitle>試験結果を記録</DialogTitle></DialogHeader>
                <div className="grid gap-4 py-2">
                  <div className="grid gap-2"><label className="text-sm font-medium">資格</label><Select value={qualName} onValueChange={setQualName}><SelectTrigger><SelectValue placeholder="選択" /></SelectTrigger><SelectContent>{QUALIFICATION_PRESETS.map((q) => <SelectItem key={q} value={q}>{q}</SelectItem>)}</SelectContent></Select>{qualName === "その他" && <Input placeholder="資格名を入力" value={customName} onChange={(e) => setCustomName(e.target.value)} />}</div>
                  {qualName && resolvedQualName && (
                    <>
                      {isToeic(resolvedQualName) ? (
                        <div className="grid grid-cols-2 gap-4">
                          <div className="grid gap-2"><label className="text-sm font-medium">Listening</label><Input type="number" min={5} max={495} step={5} placeholder="例: 400" value={listening} onChange={(e) => setListening(e.target.value)} /></div>
                          <div className="grid gap-2"><label className="text-sm font-medium">Reading</label><Input type="number" min={5} max={495} step={5} placeholder="例: 385" value={reading} onChange={(e) => setReading(e.target.value)} /></div>
                        </div>
                      ) : (
                        <div className="grid gap-2"><label className="text-sm font-medium">結果</label><Input placeholder="例: 2級合格, 85点" value={resultText} onChange={(e) => setResultText(e.target.value)} /></div>
                      )}
                      <div className="grid gap-2"><label className="text-sm font-medium">受験日</label><Input type="date" value={scoreDate} onChange={(e) => setScoreDate(e.target.value)} /></div>
                    </>
                  )}
                </div>
                <DialogFooter><Button variant="outline" onClick={() => setScoreOpen(false)}>キャンセル</Button><Button onClick={addRecord} disabled={!scoreDate || !resolvedQualName || (isToeic(resolvedQualName) ? !listening || !reading : !resultText.trim())}>追加</Button></DialogFooter>
              </DialogContent>
            </Dialog>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {goals.map((goal) => {
            const progress = goalProgress(goal);
            const latest = latestByQual.get(goal.qualificationName);
            return (
              <div key={goal.id} className="rounded-lg border p-3">
                <p className="font-medium">{goal.qualificationName}</p>
                <p className="text-muted-foreground text-sm">目標: {goal.target}（{formatDate(goal.targetDate)}まで）</p>
                {progress != null && <Progress value={progress} className="mt-2 h-2" />}
                {latest && <p className="text-muted-foreground mt-1 text-xs">直近: {recordDisplay(latest)} · {formatDate(latest.date)}</p>}
              </div>
            );
          })}
          {records.length === 0 && goals.length === 0 && <p className="text-muted-foreground text-center text-sm py-4">資格を選んで結果や目標を追加するとここに表示されます</p>}
          {Array.from(latestByQual.entries()).sort((a, b) => new Date(b[1].date).getTime() - new Date(a[1].date).getTime()).slice(0, 5).map(([name, r]) => (
            <div key={`${name}-${r.id}`} className="flex justify-between rounded-lg border bg-muted/30 px-3 py-2 text-sm"><span className="font-medium">{name}</span><span className="text-muted-foreground">{recordDisplay(r)} · {formatDate(r.date)}</span></div>
          ))}
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0">
          <div><CardTitle>練習記録</CardTitle><CardDescription>資格ごとの勉強時間を記録</CardDescription></div>
          <Dialog open={practiceOpen} onOpenChange={setPracticeOpen}>
            <DialogTrigger asChild><Button size="sm" className="gap-1.5"><Plus className="size-4" /> 記録</Button></DialogTrigger>
            <DialogContent>
              <DialogHeader><DialogTitle>練習を記録</DialogTitle></DialogHeader>
              <div className="grid gap-4 py-2">
                <div className="grid gap-2"><label className="text-sm font-medium">資格</label><Select value={practiceQualName} onValueChange={setPracticeQualName}><SelectTrigger><SelectValue placeholder="選択" /></SelectTrigger><SelectContent>{QUALIFICATION_PRESETS.map((q) => <SelectItem key={q} value={q}>{q}</SelectItem>)}</SelectContent></Select>{practiceQualName === "その他" && <Input placeholder="資格名を入力" value={practiceCustomName} onChange={(e) => setPracticeCustomName(e.target.value)} />}</div>
                <div className="grid gap-2"><label className="text-sm font-medium">勉強時間（分）</label><Input type="number" min={1} placeholder="例: 30" value={practiceMinutes} onChange={(e) => setPracticeMinutes(e.target.value)} /></div>
                {practiceQualName === "TOEIC" && <div className="grid gap-2"><label className="text-sm font-medium">種類</label><Select value={practiceType} onValueChange={(v) => setPracticeType(v as "listening" | "reading" | "both")}><SelectTrigger><SelectValue /></SelectTrigger><SelectContent><SelectItem value="listening">リスニング</SelectItem><SelectItem value="reading">リーディング</SelectItem><SelectItem value="both">両方</SelectItem></SelectContent></Select></div>}
                <div className="grid gap-2"><label className="text-sm font-medium">日付</label><Input type="date" value={practiceDate} onChange={(e) => setPracticeDate(e.target.value)} /></div>
              </div>
              <DialogFooter><Button variant="outline" onClick={() => setPracticeOpen(false)}>キャンセル</Button><Button onClick={addPractice} disabled={!practiceQualName || !practiceDate || !practiceMinutes || Number.isNaN(parseInt(practiceMinutes, 10)) || parseInt(practiceMinutes, 10) < 1 || (practiceQualName === "その他" && !practiceCustomName.trim())}>追加</Button></DialogFooter>
            </DialogContent>
          </Dialog>
        </CardHeader>
        <CardContent>
          {practices.length === 0 ? <p className="text-muted-foreground text-sm py-2">練習記録がありません</p> : <ul className="mt-3 space-y-1.5 text-sm">{[...practices].reverse().slice(0, 7).map((p) => (<li key={p.id} className="flex justify-between text-muted-foreground">{p.qualificationName} · {formatDate(p.date)} · {p.minutes}分{p.type && ` · ${p.type === "both" ? "両方" : p.type === "listening" ? "L" : "R"}`}</li>))}</ul>}
        </CardContent>
      </Card>
    </div>
  );
}
