// 大学生の勉強・課題・資格・習慣 一括管理用の型定義

export type QualificationRecord = {
  id: string;
  name: string;
  date: string;
  toeicListening?: number;
  toeicReading?: number;
  result?: string;
  memo?: string;
};

export type QualificationGoal = {
  id: string;
  qualificationName: string;
  target: string;
  targetDate: string;
};

export type QualificationPractice = {
  id: string;
  qualificationName: string;
  date: string;
  minutes: number;
  type?: "listening" | "reading" | "both";
  memo?: string;
};

export const QUALIFICATION_PRESETS = [
  "TOEIC",
  "英検",
  "簿記3級",
  "簿記2級",
  "基本情報技術者",
  "FP3級",
  "FP2級",
  "その他",
] as const;

export type Assignment = {
  id: string;
  title: string;
  subject?: string;
  dueDate: string;
  priority: "low" | "medium" | "high";
  completed: boolean;
  memo?: string;
};

export type StudyLog = {
  id: string;
  date: string;
  minutes: number;
  subject?: string;
  memo?: string;
};

// 日常タスク（大学以外も含めた普段の ToDo）
export type DailyTask = {
  id: string;
  title: string;
  // 例: 「買い物」「アルバイト」「趣味」など
  category?: string;
  // 期限（日付）。カウントダウン表示に利用
  dueDate: string;
  completed: boolean;
  memo?: string;
};

export type Habit = {
  id: string;
  name: string;
  targetDays: number[];
  streak: number;
  completedDates: string[];
};

export type AppData = {
  qualificationRecords: QualificationRecord[];
  qualificationGoals: QualificationGoal[];
  qualificationPractices: QualificationPractice[];
  assignments: Assignment[];
  studyLogs: StudyLog[];
  habits: Habit[];
  dailyTasks: DailyTask[];
};

export const DEFAULT_APP_DATA: AppData = {
  qualificationRecords: [],
  qualificationGoals: [],
  qualificationPractices: [],
  assignments: [],
  studyLogs: [],
  habits: [],
  dailyTasks: [],
};
