"use client";

import type { AppData } from "./types";
import { DEFAULT_APP_DATA } from "./types";

const STORAGE_KEY = "study-hub-data";

export function loadData(): AppData {
  if (typeof window === "undefined") return DEFAULT_APP_DATA;
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (!raw) return DEFAULT_APP_DATA;
    const parsed = JSON.parse(raw) as Partial<AppData>;
    return {
      qualificationRecords: parsed.qualificationRecords ?? DEFAULT_APP_DATA.qualificationRecords,
      qualificationGoals: parsed.qualificationGoals ?? DEFAULT_APP_DATA.qualificationGoals,
      qualificationPractices: parsed.qualificationPractices ?? DEFAULT_APP_DATA.qualificationPractices,
      assignments: parsed.assignments ?? DEFAULT_APP_DATA.assignments,
      studyLogs: parsed.studyLogs ?? DEFAULT_APP_DATA.studyLogs,
      habits: parsed.habits ?? DEFAULT_APP_DATA.habits,
      dailyTasks: parsed.dailyTasks ?? DEFAULT_APP_DATA.dailyTasks,
    };
  } catch {
    return DEFAULT_APP_DATA;
  }
}

export function saveData(data: AppData): void {
  if (typeof window === "undefined") return;
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
  } catch {
    // ignore
  }
}
