"use client";

import { useEffect, useState } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { BookOpen, ClipboardList, Timer, Flame, ListTodo, AlarmClock, Calendar as CalendarIcon } from "lucide-react";
import { loadData, saveData } from "@/lib/storage";
import type { AppData } from "@/lib/types";
import { QualificationSection } from "./QualificationSection";
import { AssignmentsSection } from "./AssignmentsSection";
import { StudyTimeSection } from "./StudyTimeSection";
import { HabitsSection } from "./HabitsSection";
import { DailyTasksSection } from "./DailyTasksSection";
import { CalendarSection } from "./CalendarSection";
import { DEFAULT_APP_DATA } from "@/lib/types";

export function Dashboard() {
  const [data, setData] = useState<AppData>(DEFAULT_APP_DATA);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setData(loadData());
    setMounted(true);
  }, []);

  useEffect(() => {
    if (!mounted) return;
    saveData(data);
  }, [data, mounted]);

  const updateData = (updater: (prev: AppData) => AppData) => setData(updater);

  if (!mounted) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <p className="text-muted-foreground">読み込み中...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/30">
      <header className="border-b bg-card/80 backdrop-blur">
        <div className="mx-auto max-w-4xl px-4 py-5">
          <h1 className="text-xl font-bold tracking-tight sm:text-2xl">勉強ハブ</h1>
          <p className="text-muted-foreground text-sm">大学生の勉強・課題・資格・習慣を一括管理</p>
        </div>
      </header>
      <main className="mx-auto max-w-4xl px-4 py-6 space-y-4">
        {/* 期限カウントダウンのサマリ */}
        <section className="rounded-xl border bg-card/80 px-4 py-3 text-sm shadow-sm">
          <div className="flex items-center gap-2 mb-2">
            <AlarmClock className="size-4 text-muted-foreground" />
            <p className="font-medium">期限が近いタスク</p>
          </div>
          <DeadlineOverview
            assignments={data.assignments}
            dailyTasks={data.dailyTasks}
          />
        </section>

        <Tabs defaultValue="calendar" className="w-full">
          <TabsList className="mb-6 grid w-full grid-cols-6 bg-muted/80 p-1">
            <TabsTrigger value="calendar" className="gap-1.5 text-xs sm:gap-2 sm:text-sm">
              <CalendarIcon className="size-4" />
              <span className="hidden sm:inline">カレンダー</span>
            </TabsTrigger>
            <TabsTrigger value="assignments" className="gap-1.5 text-xs sm:gap-2 sm:text-sm">
              <ClipboardList className="size-4" />
              <span className="hidden sm:inline">課題</span>
            </TabsTrigger>
            <TabsTrigger value="daily" className="gap-1.5 text-xs sm:gap-2 sm:text-sm">
              <ListTodo className="size-4" />
              <span className="hidden sm:inline">日常</span>
            </TabsTrigger>
            <TabsTrigger value="qualifications" className="gap-1.5 text-xs sm:gap-2 sm:text-sm"><BookOpen className="size-4" /><span className="hidden sm:inline">資格</span></TabsTrigger>
            <TabsTrigger value="study" className="gap-1.5 text-xs sm:gap-2 sm:text-sm"><Timer className="size-4" /><span className="hidden sm:inline">勉強時間</span></TabsTrigger>
            <TabsTrigger value="habits" className="gap-1.5 text-xs sm:gap-2 sm:text-sm"><Flame className="size-4" /><span className="hidden sm:inline">習慣</span></TabsTrigger>
          </TabsList>
          <TabsContent value="calendar" className="mt-0">
            <CalendarSection assignments={data.assignments} dailyTasks={data.dailyTasks} />
          </TabsContent>
          <TabsContent value="assignments" className="mt-0">
            <AssignmentsSection
              assignments={data.assignments}
              onUpdate={(assignments) =>
                updateData((d) => ({ ...d, assignments }))
              }
            />
          </TabsContent>
          <TabsContent value="daily" className="mt-0">
            <DailyTasksSection
              tasks={data.dailyTasks}
              onUpdate={(dailyTasks) =>
                updateData((d) => ({ ...d, dailyTasks }))
              }
            />
          </TabsContent>
          <TabsContent value="qualifications" className="mt-0"><QualificationSection records={data.qualificationRecords} goals={data.qualificationGoals} practices={data.qualificationPractices} onUpdate={(payload) => updateData((d) => ({ ...d, ...payload }))} /></TabsContent>
          <TabsContent value="study" className="mt-0"><StudyTimeSection logs={data.studyLogs} onUpdate={(studyLogs) => updateData((d) => ({ ...d, studyLogs }))} /></TabsContent>
          <TabsContent value="habits" className="mt-0"><HabitsSection habits={data.habits} onUpdate={(habits) => updateData((d) => ({ ...d, habits }))} /></TabsContent>
        </Tabs>
      </main>
    </div>
  );
}

// すべてのタスクの期限カウントダウン（上部サマリ用）
import type { Assignment, DailyTask } from "@/lib/types";
import { Badge } from "@/components/ui/badge";

function daysUntil(date: string) {
  const today = new Date();
  today.setHours(0, 0, 0, 0);
  const d = new Date(date + "T00:00:00");
  const diff = d.getTime() - today.getTime();
  return Math.floor(diff / (1000 * 60 * 60 * 24));
}

function formatCountdown(due: string) {
  const d = daysUntil(due);
  if (d < 0) return "期限切れ";
  if (d === 0) return "今日まで";
  if (d === 1) return "あと1日";
  return `あと${d}日`;
}

function DeadlineOverview({
  assignments,
  dailyTasks,
}: {
  assignments: Assignment[];
  dailyTasks: DailyTask[];
}) {
  const items = [
    ...assignments
      .filter((a) => !a.completed)
      .map((a) => ({
        id: `a-${a.id}`,
        kind: "課題" as const,
        label: a.title,
        date: a.dueDate,
      })),
    ...dailyTasks
      .filter((t) => !t.completed)
      .map((t) => ({
        id: `d-${t.id}`,
        kind: "日常" as const,
        label: t.title,
        date: t.dueDate,
      })),
  ]
    .filter((x) => x.date)
    .sort(
      (a, b) =>
        new Date(a.date).getTime() - new Date(b.date).getTime(),
    )
    .slice(0, 5);

  if (items.length === 0) {
    return (
      <p className="text-muted-foreground text-xs">
        期限が設定された未完了タスクはありません。
      </p>
    );
  }

  return (
    <ul className="space-y-1 text-xs">
      {items.map((item) => (
        <li
          key={item.id}
          className="flex items-center justify-between gap-2"
        >
          <div className="flex items-center gap-2 min-w-0">
            <Badge
              variant="outline"
              className="border-muted-foreground/40 text-[10px] px-1.5 py-0"
            >
              {item.kind}
            </Badge>
            <span className="truncate">{item.label}</span>
          </div>
          <div className="flex items-center gap-2 shrink-0">
            <span className="text-muted-foreground">
              {new Date(item.date + "T12:00:00").toLocaleDateString(
                "ja-JP",
                { month: "numeric", day: "numeric" },
              )}
            </span>
            <span className="font-medium text-xs">
              {formatCountdown(item.date)}
            </span>
          </div>
        </li>
      ))}
    </ul>
  );
}
