"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog";
import type { Habit } from "@/lib/types";
import { Plus, Flame } from "lucide-react";

const DAYS = ["日", "月", "火", "水", "木", "金", "土"];

function todayKey() {
  return new Date().toISOString().slice(0, 10);
}

function calcStreak(completedDates: string[]): number {
  const key = todayKey();
  if (!completedDates.includes(key)) return 0;
  let count = 0;
  const d = new Date(key);
  for (;;) {
    const k = d.toISOString().slice(0, 10);
    if (!completedDates.includes(k)) break;
    count++;
    d.setDate(d.getDate() - 1);
  }
  return count;
}

function toggleHabitToday(habit: Habit): Habit {
  const key = todayKey();
  const has = habit.completedDates.includes(key);
  const newDates = has ? habit.completedDates.filter((d) => d !== key) : [...habit.completedDates, key];
  return { ...habit, completedDates: newDates, streak: calcStreak(newDates) };
}

export function HabitsSection({ habits, onUpdate }: { habits: Habit[]; onUpdate: (h: Habit[]) => void }) {
  const [open, setOpen] = useState(false);
  const [name, setName] = useState("");
  const [selectedDays, setSelectedDays] = useState<number[]>([1, 2, 3, 4, 5]);

  const toggleDay = (d: number) => setSelectedDays((prev) => (prev.includes(d) ? prev.filter((x) => x !== d) : [...prev, d].sort((a, b) => a - b)));

  const add = () => {
    if (!name.trim() || selectedDays.length === 0) return;
    onUpdate([...habits, { id: crypto.randomUUID(), name: name.trim(), targetDays: selectedDays, streak: 0, completedDates: [] }]);
    setName("");
    setSelectedDays([1, 2, 3, 4, 5]);
    setOpen(false);
  };

  const toggle = (habit: Habit) => onUpdate(habits.map((h) => (h.id === habit.id ? toggleHabitToday(h) : h)));

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between space-y-0">
        <div>
          <CardTitle className="flex items-center gap-2"><Flame className="size-5" /> 習慣管理</CardTitle>
          <CardDescription>毎日の習慣をチェックしてストリークを伸ばす</CardDescription>
        </div>
        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger asChild><Button size="sm" className="gap-1.5"><Plus className="size-4" /> 習慣を追加</Button></DialogTrigger>
          <DialogContent>
            <DialogHeader><DialogTitle>新しい習慣</DialogTitle></DialogHeader>
            <div className="grid gap-4 py-2">
              <div className="grid gap-2"><label className="text-sm font-medium">習慣の名前</label><Input placeholder="例: 英単語20個" value={name} onChange={(e) => setName(e.target.value)} /></div>
              <div className="grid gap-2"><label className="text-sm font-medium">実施する曜日</label>
                <div className="flex flex-wrap gap-2">{DAYS.map((label, i) => (
                  <label key={i} className="flex cursor-pointer items-center gap-2 rounded-md border px-3 py-2 has-[:checked]:bg-primary has-[:checked]:text-primary-foreground has-[:checked]:border-primary">
                    <Checkbox checked={selectedDays.includes(i)} onCheckedChange={() => toggleDay(i)} /><span className="text-sm">{label}</span>
                  </label>
                ))}</div>
              </div>
            </div>
            <DialogFooter><Button variant="outline" onClick={() => setOpen(false)}>キャンセル</Button><Button onClick={add} disabled={!name.trim() || selectedDays.length === 0}>追加</Button></DialogFooter>
          </DialogContent>
        </Dialog>
      </CardHeader>
      <CardContent>
        {habits.length === 0 ? <p className="text-muted-foreground text-center text-sm py-8">習慣を追加するとここに表示されます</p> : (
          <ul className="space-y-3">
            {habits.map((habit) => {
              const done = habit.completedDates.includes(todayKey());
              const todayDay = new Date().getDay();
              const isTargetDay = habit.targetDays.includes(todayDay);
              return (
                <li key={habit.id} className="flex items-center gap-4 rounded-lg border p-4">
                  <Checkbox checked={done} onCheckedChange={() => toggle(habit)} disabled={!isTargetDay} />
                  <div className="min-w-0 flex-1">
                    <p className="font-medium">{habit.name}</p>
                    <p className="text-muted-foreground text-xs">{habit.targetDays.map((d) => DAYS[d]).join("・")} · 連続 {habit.streak}日</p>
                  </div>
                  {habit.streak > 0 && <span className="rounded-full bg-amber-500/20 px-2.5 py-0.5 text-xs font-medium text-amber-700 dark:text-amber-300">🔥 {habit.streak}</span>}
                  {!isTargetDay && <span className="text-muted-foreground text-xs">今日は対象外</span>}
                </li>
              );
            })}
          </ul>
        )}
      </CardContent>
    </Card>
  );
}
