 "use client";

import { useState } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
} from "@/components/ui/dialog";
import type { DailyTask } from "@/lib/types";
import { Plus, Trash2, SunMedium } from "lucide-react";
import { cn } from "@/lib/utils";

function formatDate(s: string) {
  return new Date(s + "T12:00:00").toLocaleDateString("ja-JP", {
    month: "short",
    day: "numeric",
    weekday: "short",
  });
}

function diffDays(dueDate: string) {
  const today = new Date();
  today.setHours(0, 0, 0, 0);
  const due = new Date(dueDate + "T00:00:00");
  const diffMs = due.getTime() - today.getTime();
  return Math.floor(diffMs / (1000 * 60 * 60 * 24));
}

function countdownLabel(dueDate: string, completed: boolean) {
  if (completed) return "完了済み";
  const days = diffDays(dueDate);
  if (days < 0) return "期限切れ";
  if (days === 0) return "今日が期限";
  if (days === 1) return "あと1日";
  return `あと${days}日`;
}

export function DailyTasksSection({
  tasks,
  onUpdate,
}: {
  tasks: DailyTask[];
  onUpdate: (tasks: DailyTask[]) => void;
}) {
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState("");
  const [category, setCategory] = useState("");
  const [dueDate, setDueDate] = useState("");
  const [memo, setMemo] = useState("");

  const add = () => {
    if (!title.trim() || !dueDate) return;
    const next: DailyTask = {
      id: crypto.randomUUID(),
      title: title.trim(),
      category: category.trim() || undefined,
      dueDate,
      completed: false,
      memo: memo.trim() || undefined,
    };
    onUpdate([...tasks, next]);
    setTitle("");
    setCategory("");
    setDueDate("");
    setMemo("");
    setOpen(false);
  };

  const toggle = (id: string) => {
    onUpdate(
      tasks.map((t) =>
        t.id === id ? { ...t, completed: !t.completed } : t,
      ),
    );
  };

  const remove = (id: string) => onUpdate(tasks.filter((t) => t.id !== id));

  const sorted = [...tasks].sort(
    (a, b) => new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime(),
  );
  const pending = sorted.filter((t) => !t.completed);
  const done = sorted.filter((t) => t.completed);

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between space-y-0">
        <div>
          <CardTitle className="flex items-center gap-2">
            <SunMedium className="size-5" />
            日常タスク
          </CardTitle>
          <CardDescription>
            日常生活やアルバイトなどのタスクをまとめて管理
          </CardDescription>
        </div>
        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger asChild>
            <Button size="sm" className="gap-1.5">
              <Plus className="size-4" />
              追加
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>日常タスクを追加</DialogTitle>
            </DialogHeader>
            <div className="grid gap-4 py-2">
              <div className="grid gap-2">
                <label className="text-sm font-medium">タスク名</label>
                <Input
                  placeholder="例: 買い物, アルバイト, 友達と予定"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                />
              </div>
              <div className="grid gap-2">
                <label className="text-sm font-medium">カテゴリ（任意）</label>
                <Input
                  placeholder="例: 家事, アルバイト, 趣味"
                  value={category}
                  onChange={(e) => setCategory(e.target.value)}
                />
              </div>
              <div className="grid gap-2">
                <label className="text-sm font-medium">期限</label>
                <Input
                  type="date"
                  value={dueDate}
                  onChange={(e) => setDueDate(e.target.value)}
                />
              </div>
              <div className="grid gap-2">
                <label className="text-sm font-medium">メモ（任意）</label>
                <Input
                  placeholder="例: 必要な物、集合場所 など"
                  value={memo}
                  onChange={(e) => setMemo(e.target.value)}
                />
              </div>
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setOpen(false)}>
                キャンセル
              </Button>
              <Button
                onClick={add}
                disabled={!title.trim() || !dueDate}
              >
                追加
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </CardHeader>
      <CardContent className="space-y-4">
        {tasks.length === 0 ? (
          <p className="text-muted-foreground text-center text-sm py-8">
            タスクを追加するとここに表示されます
          </p>
        ) : (
          <>
            {pending.length > 0 && (
              <ul className="space-y-2">
                {pending.map((t) => {
                  const days = diffDays(t.dueDate);
                  const isOverdue = days < 0 && !t.completed;
                  const isToday = days === 0 && !t.completed;
                  return (
                    <li
                      key={t.id}
                      className={cn(
                        "flex items-center gap-3 rounded-lg border p-3",
                        isOverdue &&
                          "border-destructive/50 bg-destructive/5",
                        isToday &&
                          "border-amber-500/50 bg-amber-500/5",
                      )}
                    >
                      <Checkbox
                        checked={t.completed}
                        onCheckedChange={() => toggle(t.id)}
                      />
                      <div className="min-w-0 flex-1">
                        <p className="font-medium truncate">{t.title}</p>
                        <p className="text-muted-foreground text-xs">
                          {t.category && `${t.category} · `}
                          {formatDate(t.dueDate)} ·{" "}
                          {countdownLabel(t.dueDate, t.completed)}
                        </p>
                        {t.memo && (
                          <p className="text-muted-foreground mt-0.5 text-xs line-clamp-2">
                            {t.memo}
                          </p>
                        )}
                      </div>
                      {isOverdue && (
                        <Badge
                          variant="secondary"
                          className="bg-destructive/15 text-destructive"
                        >
                          期限切れ
                        </Badge>
                      )}
                      <Button
                        variant="ghost"
                        size="icon"
                        className="shrink-0 text-muted-foreground hover:text-destructive"
                        onClick={() => remove(t.id)}
                      >
                        <Trash2 className="size-4" />
                      </Button>
                    </li>
                  );
                })}
              </ul>
            )}
            {done.length > 0 && (
              <div>
                <p className="text-muted-foreground mb-2 text-xs font-medium">
                  完了 ({done.length})
                </p>
                <ul className="space-y-2">
                  {done.map((t) => (
                    <li
                      key={t.id}
                      className="flex items-center gap-3 rounded-lg border border-dashed p-3 opacity-80"
                    >
                      <Checkbox
                        checked
                        onCheckedChange={() => toggle(t.id)}
                      />
                      <div className="min-w-0 flex-1">
                        <p className="truncate text-sm line-through text-muted-foreground">
                          {t.title}
                        </p>
                        <p className="text-muted-foreground text-xs">
                          {formatDate(t.dueDate)}
                        </p>
                      </div>
                      <Button
                        variant="ghost"
                        size="icon"
                        className="shrink-0"
                        onClick={() => remove(t.id)}
                      >
                        <Trash2 className="size-4" />
                      </Button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </>
        )}
      </CardContent>
    </Card>
  );
}

