"use client";

import { useMemo } from "react";
import { Bar, BarChart, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from "recharts";
import { ChartContainer, ChartTooltip, ChartTooltipContent } from "@/components/ui/chart";
import type { ChartConfig } from "@/components/ui/chart";
import type { StudyLog } from "@/lib/types";

const SUBJECT_COLORS = [
  "hsl(221, 83%, 53%)",
  "hsl(142, 71%, 45%)",
  "hsl(38, 92%, 50%)",
  "hsl(346, 77%, 50%)",
  "hsl(262, 83%, 58%)",
  "hsl(189, 94%, 43%)",
  "hsl(25, 95%, 53%)",
  "hsl(173, 80%, 40%)",
  "hsl(330, 81%, 60%)",
  "hsl(47, 96%, 53%)",
];

const UNCATEGORIZED = "未分類";

type Props = { logs: StudyLog[]; days?: number };

function formatDateShort(s: string) {
  const d = new Date(s);
  return `${d.getMonth() + 1}/${d.getDate()}`;
}

export function StudyTimeChart({ logs, days = 14 }: Props) {
  const { chartData, config, subjectOrder } = useMemo(() => {
    if (logs.length === 0) return { chartData: [] as Record<string, string | number>[], config: {} as ChartConfig, subjectOrder: [] as string[] };
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    const start = new Date(today);
    start.setDate(start.getDate() - (days - 1));
    const byDate = new Map<string, Map<string, number>>();
    for (const log of logs) {
      const d = new Date(log.date);
      d.setHours(0, 0, 0, 0);
      if (d < start) continue;
      const dateKey = log.date;
      const subject = log.subject?.trim() || UNCATEGORIZED;
      const map = byDate.get(dateKey) ?? new Map<string, number>();
      map.set(subject, (map.get(subject) ?? 0) + log.minutes);
      byDate.set(dateKey, map);
    }
    const allSubjects = new Set<string>();
    byDate.forEach((m) => m.forEach((_, subj) => allSubjects.add(subj)));
    const subjectOrder = Array.from(allSubjects).sort((a, b) => (a === UNCATEGORIZED ? 1 : b === UNCATEGORIZED ? -1 : a.localeCompare(b)));
    const subjectToKey = new Map<string, string>();
    subjectOrder.forEach((subj, i) => subjectToKey.set(subj, `s${i}`));
    const dateKeys: string[] = [];
    for (let i = 0; i < days; i++) {
      const d = new Date(start);
      d.setDate(d.getDate() + i);
      dateKeys.push(d.toISOString().slice(0, 10));
    }
    const chartData = dateKeys.map((dateKey) => {
      const row: Record<string, string | number> = { date: formatDateShort(dateKey), dateKey };
      const map = byDate.get(dateKey);
      for (const subj of subjectOrder) row[subjectToKey.get(subj)!] = map?.get(subj) ?? 0;
      return row;
    });
    const config: ChartConfig = {};
    subjectOrder.forEach((subj, i) => {
      config[`s${i}`] = { label: subj, color: SUBJECT_COLORS[i % SUBJECT_COLORS.length] };
    });
    return { chartData, config, subjectOrder };
  }, [logs, days]);

  if (logs.length === 0) {
    return (
      <div className="flex min-h-[240px] items-center justify-center rounded-lg border border-dashed bg-muted/20">
        <p className="text-muted-foreground text-sm">記録を追加するとグラフが表示されます</p>
      </div>
    );
  }

  return (
    <ChartContainer config={config} className="h-[280px] w-full">
      <ResponsiveContainer width="100%" height="100%">
        <BarChart data={chartData} margin={{ top: 8, right: 8, left: 8, bottom: 8 }}>
          <CartesianGrid strokeDasharray="3 3" className="stroke-muted" vertical={false} />
          <XAxis dataKey="date" tickLine={false} axisLine={false} tick={{ fill: "hsl(var(--muted-foreground))" }} />
          <YAxis tickLine={false} axisLine={false} tick={{ fill: "hsl(var(--muted-foreground))" }} tickFormatter={(v) => `${v}分`} />
          <ChartTooltip
            content={
              <ChartTooltipContent
                formatter={(value) => [`${Number(value)}分`, undefined]}
                labelFormatter={(_, payload) => (payload?.[0]?.payload?.dateKey ? new Date((payload[0].payload as { dateKey: string }).dateKey).toLocaleDateString("ja-JP", { month: "long", day: "numeric", weekday: "short" }) : "")}
              />
            }
          />
          <Legend wrapperStyle={{ paddingTop: 12 }} formatter={(value) => config[value]?.label ?? value} iconType="square" iconSize={10} />
          {subjectOrder.map((subj, i) => (
            <Bar key={subj} dataKey={`s${i}`} name={subj} stackId="a" fill={`var(--color-s${i})`} radius={i === subjectOrder.length - 1 ? [0, 4, 4, 0] : [0, 0, 0, 0]} />
          ))}
        </BarChart>
      </ResponsiveContainer>
    </ChartContainer>
  );
}
