"use client";

import { useMemo, useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Calendar } from "@/components/ui/calendar";
import type { Assignment, DailyTask } from "@/lib/types";

type CalendarSectionProps = {
  assignments: Assignment[];
  dailyTasks: DailyTask[];
};

type CalendarItem =
  | { type: "assignment"; id: string; title: string; subject?: string; dueDate: string }
  | { type: "daily"; id: string; title: string; category?: string; dueDate: string };

function toYmd(d: Date): string {
  const d2 = new Date(d);
  d2.setHours(0, 0, 0, 0);
  return d2.toISOString().slice(0, 10);
}

export function CalendarSection({ assignments, dailyTasks }: CalendarSectionProps) {
  const [selected, setSelected] = useState<Date | undefined>(new Date());

  // yyyy-mm-dd -> items[] のマップを作る
  const byDate = useMemo(() => {
    const map = new Map<string, CalendarItem[]>();

    for (const a of assignments) {
      if (!a.dueDate) continue;
      const list = map.get(a.dueDate) ?? [];
      list.push({
        type: "assignment",
        id: a.id,
        title: a.title,
        subject: a.subject,
        dueDate: a.dueDate,
      });
      map.set(a.dueDate, list);
    }

    for (const t of dailyTasks) {
      if (!t.dueDate) continue;
      const list = map.get(t.dueDate) ?? [];
      list.push({
        type: "daily",
        id: t.id,
        title: t.title,
        category: t.category,
        dueDate: t.dueDate,
      });
      map.set(t.dueDate, list);
    }

    return map;
  }, [assignments, dailyTasks]);

  const selectedYmd = selected ? toYmd(selected) : undefined;
  const itemsForSelected = selectedYmd ? byDate.get(selectedYmd) ?? [] : [];

  return (
    <Card>
      <CardHeader>
        <CardTitle>カレンダー</CardTitle>
        <CardDescription>課題と日常タスクの期限をカレンダーで確認</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="flex flex-col gap-6 md:flex-row">
          <div className="md:w-1/2">
            <Calendar
              mode="single"
              selected={selected}
              onSelect={(day) => day && setSelected(day)}
              modifiers={{
                hasTask: (day) => byDate.has(toYmd(day)),
              }}
              modifiersClassNames={{
                hasTask: "after:absolute after:bottom-1 after:left-1/2 after:h-1.5 after:w-1.5 after:-translate-x-1/2 after:rounded-full after:bg-primary",
              }}
            />
          </div>
          <div className="md:w-1/2 space-y-3">
            <div>
              <p className="text-xs text-muted-foreground mb-1">
                選択した日
              </p>
              <p className="font-semibold">
                {selected
                  ? selected.toLocaleDateString("ja-JP", {
                      year: "numeric",
                      month: "long",
                      day: "numeric",
                      weekday: "short",
                    })
                  : "日付を選択してください"}
              </p>
            </div>
            {itemsForSelected.length === 0 ? (
              <p className="text-muted-foreground text-sm">
                この日には登録されている課題・日常タスクはありません。
              </p>
            ) : (
              <ul className="space-y-2 text-sm">
                {itemsForSelected.map((item) => (
                  <li
                    key={`${item.type}-${item.id}`}
                    className="flex items-center justify-between rounded-lg border px-3 py-2"
                  >
                    <div className="min-w-0">
                      <p className="font-medium truncate">
                        {item.title}
                      </p>
                      <p className="text-xs text-muted-foreground">
                        {item.type === "assignment"
                          ? item.subject ?? "課題"
                          : item.category ?? "日常タスク"}
                      </p>
                    </div>
                    <span className="text-[11px] rounded-full border px-2 py-0.5 text-muted-foreground">
                      {item.type === "assignment" ? "課題" : "日常"}
                    </span>
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  );
}

