"use client";

import { useState, useEffect, useCallback, useRef } from "react";
import { Card } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import { Play, Pause, RotateCcw } from "lucide-react";

export type PomodoroMode = "pomodoro" | "shortBreak" | "longBreak";

const SHORT_BREAK_MIN = 5;
const LONG_BREAK_MIN = 15;
const POMODORO_MINUTES_OPTIONS = [15, 25, 50] as const;
const TARGET_SETS_OPTIONS = [2, 3, 4, 5, 6] as const;

type Props = {
  onPomodoroComplete?: (minutes: number) => void;
};

export function PomodoroTimer({ onPomodoroComplete }: Props) {
  const [mode, setMode] = useState<PomodoroMode>("pomodoro");
  const [pomodoroMinutes, setPomodoroMinutes] = useState(25);
  const [targetSets, setTargetSets] = useState(4);
  const [secondsLeft, setSecondsLeft] = useState(25 * 60);
  const [running, setRunning] = useState(false);
  const [completedInSession, setCompletedInSession] = useState(0);
  const onCompleteRef = useRef(onPomodoroComplete);
  onCompleteRef.current = onPomodoroComplete;

  const getMinutes = useCallback(
    (m: PomodoroMode) =>
      m === "pomodoro" ? pomodoroMinutes : m === "shortBreak" ? SHORT_BREAK_MIN : LONG_BREAK_MIN,
    [pomodoroMinutes]
  );
  const totalSeconds = getMinutes(mode) * 60;

  const resetTimer = useCallback(
    (newMode?: PomodoroMode) => {
      const m = newMode ?? mode;
      setSecondsLeft(getMinutes(m) * 60);
      setRunning(false);
    },
    [mode, getMinutes]
  );

  const switchMode = useCallback(
    (newMode: PomodoroMode) => {
      setMode(newMode);
      setSecondsLeft(getMinutes(newMode) * 60);
      setRunning(false);
    },
    [getMinutes]
  );

  useEffect(() => {
    setSecondsLeft(getMinutes(mode) * 60);
  }, [mode, getMinutes]);

  useEffect(() => {
    if (!running) return;
    const id = setInterval(() => {
      setSecondsLeft((prev) => {
        if (prev <= 1) {
          setRunning(false);
          if (mode === "pomodoro") {
            setCompletedInSession((n) => n + 1);
            onCompleteRef.current?.(pomodoroMinutes);
          }
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    return () => clearInterval(id);
  }, [running, mode, pomodoroMinutes]);

  const progress = totalSeconds > 0 ? 100 - (secondsLeft / totalSeconds) * 100 : 100;
  const isBreak = mode === "shortBreak" || mode === "longBreak";

  return (
    <Card className="overflow-hidden border-2 bg-gradient-to-b from-card to-muted/20">
      <div className="p-4 pb-2">
        <p className="text-muted-foreground text-center text-xs font-medium uppercase tracking-wider">
          Pomodoro
        </p>
        {!running && (
          <div className="mt-3 space-y-2 rounded-lg border bg-muted/30 p-3">
            <div className="flex flex-wrap items-center gap-2">
              <span className="text-muted-foreground text-xs">集中:</span>
              {POMODORO_MINUTES_OPTIONS.map((min) => (
                <Button
                  key={min}
                  type="button"
                  size="sm"
                  variant={pomodoroMinutes === min ? "default" : "outline"}
                  className="h-7 px-2 text-xs"
                  onClick={() => {
                    setPomodoroMinutes(min);
                    if (mode === "pomodoro") setSecondsLeft(min * 60);
                  }}
                >
                  {min}分
                </Button>
              ))}
            </div>
            <div className="flex flex-wrap items-center gap-2">
              <span className="text-muted-foreground text-xs">目標セット:</span>
              {TARGET_SETS_OPTIONS.map((n) => (
                <Button
                  key={n}
                  type="button"
                  size="sm"
                  variant={targetSets === n ? "default" : "outline"}
                  className="h-7 px-2 text-xs"
                  onClick={() => setTargetSets(n)}
                >
                  {n}セット
                </Button>
              ))}
            </div>
          </div>
        )}
        <Tabs value={mode} onValueChange={(v) => switchMode(v as PomodoroMode)} className="w-full">
          <TabsList className="mx-auto mt-2 grid w-full max-w-xs grid-cols-3 bg-muted/60 p-1">
            <TabsTrigger value="pomodoro" className="text-xs sm:text-sm">集中</TabsTrigger>
            <TabsTrigger value="shortBreak" className="text-xs sm:text-sm">短休憩</TabsTrigger>
            <TabsTrigger value="longBreak" className="text-xs sm:text-sm">長休憩</TabsTrigger>
          </TabsList>
          <TabsContent value="pomodoro" className="mt-0">
            <div className="relative mx-auto mt-6 flex h-48 w-48 items-center justify-center sm:h-56 sm:w-56">
              <svg className="absolute inset-0 size-full -rotate-90" viewBox="0 0 100 100">
                <circle cx="50" cy="50" r="45" fill="none" className="stroke-muted" strokeWidth="6" />
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  fill="none"
                  className="stroke-primary/80 transition-all duration-1000 ease-linear"
                  strokeWidth="6"
                  strokeDasharray={`${2 * Math.PI * 45}`}
                  strokeDashoffset={`${2 * Math.PI * 45 * (1 - progress / 100)}`}
                  strokeLinecap="round"
                />
              </svg>
              <div className="flex flex-col items-center">
                <span className="font-mono text-4xl font-light tabular-nums text-foreground sm:text-5xl">
                  {String(Math.floor(secondsLeft / 60)).padStart(2, "0")}:
                  {String(secondsLeft % 60).padStart(2, "0")}
                </span>
                <p className="text-muted-foreground mt-1 text-sm">{pomodoroMinutes}分集中</p>
                {(completedInSession > 0 || targetSets > 0) && (
                  <div className="mt-2 flex gap-1">
                    {Array.from({ length: targetSets }, (_, i) => i + 1).map((i) => (
                      <span
                        key={i}
                        className={`size-2 rounded-full ${
                          i <= completedInSession ? "bg-primary" : "bg-muted"
                        }`}
                      />
                    ))}
                  </div>
                )}
              </div>
            </div>
          </TabsContent>
          <TabsContent value="shortBreak" className="mt-0">
            <div className="relative mx-auto mt-6 flex h-48 w-48 items-center justify-center sm:h-56 sm:w-56">
              <svg className="absolute inset-0 size-full -rotate-90" viewBox="0 0 100 100">
                <circle cx="50" cy="50" r="45" fill="none" className="stroke-muted" strokeWidth="6" />
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  fill="none"
                  className="stroke-emerald-500/70 transition-all duration-1000 ease-linear"
                  strokeWidth="6"
                  strokeDasharray={`${2 * Math.PI * 45}`}
                  strokeDashoffset={`${2 * Math.PI * 45 * (1 - progress / 100)}`}
                  strokeLinecap="round"
                />
              </svg>
              <div className="flex flex-col items-center">
                <span className="font-mono text-4xl font-light tabular-nums text-foreground sm:text-5xl">
                  {String(Math.floor(secondsLeft / 60)).padStart(2, "0")}:
                  {String(secondsLeft % 60).padStart(2, "0")}
                </span>
                <p className="text-muted-foreground mt-1 text-sm">{SHORT_BREAK_MIN}分休憩</p>
              </div>
            </div>
          </TabsContent>
          <TabsContent value="longBreak" className="mt-0">
            <div className="relative mx-auto mt-6 flex h-48 w-48 items-center justify-center sm:h-56 sm:w-56">
              <svg className="absolute inset-0 size-full -rotate-90" viewBox="0 0 100 100">
                <circle cx="50" cy="50" r="45" fill="none" className="stroke-muted" strokeWidth="6" />
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  fill="none"
                  className="stroke-amber-500/70 transition-all duration-1000 ease-linear"
                  strokeWidth="6"
                  strokeDasharray={`${2 * Math.PI * 45}`}
                  strokeDashoffset={`${2 * Math.PI * 45 * (1 - progress / 100)}`}
                  strokeLinecap="round"
                />
              </svg>
              <div className="flex flex-col items-center">
                <span className="font-mono text-4xl font-light tabular-nums text-foreground sm:text-5xl">
                  {String(Math.floor(secondsLeft / 60)).padStart(2, "0")}:
                  {String(secondsLeft % 60).padStart(2, "0")}
                </span>
                <p className="text-muted-foreground mt-1 text-sm">{LONG_BREAK_MIN}分休憩</p>
              </div>
            </div>
          </TabsContent>
        </Tabs>
      </div>
      <div className="flex justify-center gap-2 pb-6 pt-2">
        <Button size="lg" variant={isBreak ? "outline" : "default"} className="gap-2 rounded-full px-6" onClick={() => setRunning(true)} disabled={running}>
          <Play className="size-4" /> 開始
        </Button>
        <Button size="lg" variant="outline" className="gap-2 rounded-full px-6" onClick={() => setRunning(false)} disabled={!running}>
          <Pause className="size-4" /> 一時停止
        </Button>
        <Button size="lg" variant="ghost" className="gap-2 rounded-full px-6" onClick={() => resetTimer()}>
          <RotateCcw className="size-4" /> リセット
        </Button>
      </div>
    </Card>
  );
}
